import LanguageDetector from 'i18next-browser-languagedetector';
import i18n from 'i18next';
import enUsTrans from './assets/locales/en-us.json';
import zhCnTrans from './assets/locales/zh-cn.json';
import zhTcTrans from './assets/locales/zh-tc.json';
import jaJpTrans from './assets/locales/ja-jp.json';
import koKrTrans from './assets/locales/ko-kr.json';
import {
  initReactI18next,
} from 'react-i18next';

i18n.use(LanguageDetector) // 
.use(initReactI18next) // init i18next
.init({
  // 
  resources: {
    en: {
      translation: enUsTrans,
    },
    zhCn: {
      translation: zhCnTrans,
    },
    zhTc: {
      translation: zhTcTrans,
    },
    ja: {
      translation: jaJpTrans,
    },
    ko: {
      translation: koKrTrans,
    },
  },
  // keyen/zh
  fallbackLng: 'zh',
  debug: false,
  interpolation: {
    escapeValue: false, // not needed for react as it escapes by default
  },
});

export default i18n;
