

import Peekable from './peekable';
import tokenize from './tokenize';
import helper from './helper';
const {unescapeString} = helper;
const {tokenizeIter, tokenizeBufferIter} = tokenize;


function _parseTokens(peekableTokens, parentId, options) {
  const {getId, dictionary, onProgress, onNodeCreated} = options;

  let anchor = null;
  let node; let property;

  while (!peekableTokens.peek().done) {
    const {type, value, row, col} = peekableTokens.peek().value;

    if (type === 'parenthesis' && value === '(') break;
    if (type === 'parenthesis' && value === ')') {
      if (node != null) onNodeCreated({node});
      return anchor;
    }

    if (type === 'semicolon' || node == null) {
      // Prepare new node

      const lastNode = node;

      node = {
        id: getId(),
        data: {},
        parentId: lastNode == null ? parentId : lastNode.id,
        children: [],
      };

      if (dictionary != null) dictionary[node.id] = node;

      if (lastNode != null) {
        onNodeCreated({node: lastNode});
        lastNode.children.push(node);
      } else {
        anchor = node;
      }
    }

    if (type === 'semicolon') {
      // Work is already done
    } else if (type === 'prop_ident') {
      if (node != null) {
        // Prepare new property

        const identifier =
          value === value.toUpperCase() ?
            value :
            value
                .split('')
                .filter((x) => x.toUpperCase() === x)
                .join('');

        if (identifier !== '') {
          if (!(identifier in node.data)) node.data[identifier] = [];
          property = node.data[identifier];
        } else {
          property = null;
        }
      }
    } else if (type === 'c_value_type') {
      if (property != null) {
        property.push(unescapeString(value.slice(1, -1)));
      }
    } else if (type === 'invalid') {
      throw new Error(`Unexpected token at ${row + 1}:${col + 1}`);
    } else {
      throw new Error(
        `Unexpected token type '${type}' at ${row + 1}:${col + 1}`,
      );
    }

    peekableTokens.next();
  }

  if (node == null) {
    anchor = node = {
      id: null,
      data: {},
      parentId: null,
      children: [],
    };
  } else {
    onNodeCreated({node});
  }

  while (!peekableTokens.peek().done) {
    const {type, value, progress} = peekableTokens.peek().value;

    if (type === 'parenthesis' && value === '(') {
      peekableTokens.next();

      const child = _parseTokens(peekableTokens, node.id, options);

      if (child != null) {
        node.children.push(child);
      }
    } else if (type === 'parenthesis' && value === ')') {
      onProgress({progress});
      break;
    }

    peekableTokens.next();
  }

  return anchor;
}

const parseTokens = function(
  tokens,
  {
    getId = ((id) => () => id++)(0),
    dictionary = null,
    onProgress = () => {},
    onNodeCreated = () => {},
  } = {},
) {
  const node = _parseTokens(new Peekable(tokens), null, {
    getId,
    dictionary,
    onProgress,
    onNodeCreated,
  });

  return node.id == null ? node.children : [node];
};

const parse = function(contents, options = {}) {
  return parseTokens(tokenizeIter(contents), options);
};

const parseBuffer = function(buffer, options = {}) {
  return parseTokens(
    tokenizeBufferIter(buffer, {encoding: options.encoding}),
    options,
  );
};

const parseLib = {
 parse,
 parseTokens,
 parseBuffer,
};

export default parseLib;

