import Cookies from 'js-cookie';
import {serverAddr} from './constant';
export function jointUrl(url) {
  return serverAddr + url;
}
export const getResponse = async (res) => {
  if (!res) {
    return {success: false};
  }
  if (!res.ok) {
    return {succcess: false, status: res.status};
  }
  return {success: true, status: res.status, data: await res.json()};
};

export const getResponseText = async (res) => {
  if (!res) {
    return {success: false};
  }
  if (!res.ok) {
    return {succcess: false, status: res.status};
  }
  return {success: true, status: res.status, data: await res.text()};
};

export const post = function(url, params) {
  return fetch(jointUrl(url), {
        method: 'post',
        body: JSON.stringify(params),
        headers: {
          'Content-type': 'application/json; charset=UTF-8',
          'Auth': Cookies.get('auth'),
        },
    }).catch((error) => {
      console.log('[error] fetch error:', error);
    });
};

export const get = function(url) {
  return fetch(jointUrl(url), {
      method: 'get',
    }).catch((error) => {
      console.log('[error] fetch error:', error);
    });
};

export const getWithoutServerAddr = function(url) {
  return fetch(url, {
      method: 'get',
    }).catch((error) => {
      console.log('[error] fetch error:', error);
    });
};
