import Cookies from 'js-cookie';

export default class WebSocketHelper {
  constructor(socketUrl, event = {}, auth = true) {
    console.log('[WebSocket] create a new websocket:', socketUrl);
    try {
      if (auth) {
        const authSession = Cookies.get('auth');
        if (!authSession || authSession === 'undefined') {
          console.log('[Auth] Fail to create websocket, no auth', authSession);
          return false;
        }
        this.ws = new WebSocket(socketUrl+ `?Auth=${authSession}`);
      } else {
        this.ws = new WebSocket(socketUrl);
      }
      // ws
      for (const e in event) {
        if (e) {
          this.ws.addEventListener(e, event[e]);
        }
      }
    } catch (e) {
      console.warn('websocket', e);
    }
  }
  // 
  subscribe(event, callback) {
    this.ws.addEventListener(event, callback);
  }
  // 
  unsubscribe(event) {
    this.ws[event] = null;
  }
  // websocket
  close() {
    this.ws.close();
    this.ws = null;
  }
  // ()socket
  start(socketUrl, event) {
    if (!socketUrl || this.ws!=null) {
      return false;
    }
    this.ws = new WebSocket(socketUrl);
    for (const e of event) {
      this.ws[e] = event[e];
    }
  }
};
