import React, {useState, useEffect, useRef} from 'react';
import ReactDOM from 'react-dom';
import {DagreGraph} from '@ant-design/charts';
import G6 from '@antv/g6';
import {useDispatch} from 'react-redux';
import {windowModalComp} from '../../util/helper';

const NodeTreeGraph =({data, width, height}) => {
  const graphRef = useRef(null);
  const [graph, setGraph] = useState(null);
  const dispatch = useDispatch();
  useEffect(()=>{
    /* eslint-disable */
    let graph = new G6.TreeGraph({
      container: ReactDOM.findDOMNode(graphRef.current),
      width,
      height,
      modes: {
        default: [
          {
            type: 'custom'
          },
          'drag-canvas',
          'zoom-canvas'
        ]
      },
      defaultNode: {
        size: 6,
        style: {
          fill: '#000',
          stroke: '#aaa'
        },
        labelCfg: {
          position: 'left',
          fontSize: 1,
          style: {
            fill: '#ced'
          }
        }
      },
      defaultEdge: {
        shape: 'cubic-horizontal',
        style: {
          stroke: '#000'
        },
        position: 'middle'
      },
      layout: {
        type: 'dendrogram',
        direction: 'TB', // H / V / LR / RL / TB / BT
        nodeSep: 10,
        rankSep: 15,
        padding: [10, 5, 10, 5],
        fitView: false,
      }
    })
    graph.node(function(node) {
      let color = '#000';
      if(node.W) {
        color = '#fff';
      }
      return {
        label: String(parseInt(node.id)/2+1),
        style: {
          fill: color,
          stroke: '#aaa'
        }
      };
    });
    // graph.on('node:click', (evt) => {
    //   const { item, target } = evt;
    //   const id = parseInt(item._cfg.id);
    //   // 
    //   if((id/2+1)%2!==0 && id!==0){
    //     windowModalComp('', '', ()=>{
    //       // go back node i
    //       console.log('go back to n', id);
    //       dispatch({type: 'kataData/gobackN', id: id});
    //     }, ()=>{}, '', '')
    //   } else {
    //     windowModalComp('', '', ()=>{}, ()=>{},'');
    //   }
    // });
    graph.data(data)
    graph.render()
    setGraph(graph);
    return ()=>{
      setGraph(null);
    }
  }, []);

  useEffect(()=> {
    if(!(graph instanceof G6.TreeGraph)) {
      // console.log('no graph', graph);
      return;
    }
    graph.changeData(data);
    graph.layout(true);
  });

  return <div id='nodegraph' ref={graphRef}></div>
}

export default NodeTreeGraph;
