// localStorage
const set = (key, value) => {
  if (!key) {
    // 
    return false;
  }
  if (typeof localStorage !== 'object') {
    // localstorage
    return false;
  }
  try {
    if (Array.isArray(value) || typeof value === 'object') {
      value = JSON.stringify(value);
    }
    localStorage.setItem(key, value);
  } catch (e) {
  console.log('[Storage] ', e);
    return false;
  }
  return true;
};

const get = (key) => {
  if (!key) {
    return false;
  }
  if (typeof localStorage !== 'object') {
    // localstorage
    return false;
  }
  return localStorage.getItem(key);
};

export {
  set,
  get,
};
