
import React from 'react';

import {convertTable} from '../sgf/index.jsx';
import {useTranslation} from 'react-i18next';
import Battle from '../../api/battle';
import {Tooltip, Button, Modal} from 'antd';
import {useSelector, useDispatch} from 'react-redux';
import {CaretLeftOutlined, CaretRightOutlined, ClearOutlined, QuestionOutlined, ForwardOutlined, BackwardOutlined} from '@ant-design/icons';
import {getSGFPlayedNodeList, addNewNode, parseNodeData} from '../../util/sgf/fn';
import './operatorPanel.css';
import {windowModalComp, stackpush, translate} from '../../util/helper.js';
import {GameMode} from '../../util/constant.js';
const {askHelp, finishBoard, sendCommonGtp} = Battle;

function checkNoSGF(gameStatus) {
  if (gameStatus['sgf'] == null) {
    Modal.info({
      title: translate(''),
      content: translate('SGF'),
      onOk: () => {},
    });
    return true;
  }
}

