// positionnodedata
function getNodeDataAtPosition(gameTrees, position) {
  // console.log('gameTrees: ', gameTrees);
  // console.log('gameTrees: ', JSON.stringify(gameTrees));
  // console.log('position: ', position);
  let node;
  if (position == 0) {
    const data = gameTrees[0]['root']['data'];
    return data;
  }
  node = [gameTrees[0]['root']];
  for (let i = 0; i < position; i++) {
    node = node[0]['children'];
  }
  const data = node[0]['data'];
  return data;
}

// 
export default function OperatorPanel(props) {
  const {gameStatus, board, setBoard, mode, shareData, startGame, historyBoardList, setHistoryBoardList, futureBoardList, setFutureBoardList, lastNode, setLastNode} = props;
  const {t} = useTranslation();
  const SGFPlayedNodeList = getSGFPlayedNodeList();
  // const dispatch = useDispatch();
  // const gobackN = useSelector((state) => state.kataData.gobackN);

  const nextMove = () =>{
    // [TODO] 
    if (mode === GameMode.SGF) {
      if (checkNoSGF(gameStatus)) {
        return;
      }
      console.log('play sgf gamestatus: ', gameStatus);
      // position: 
      const position = gameStatus['treePosition'];
      // (position0)==
      if (position == gameStatus['stepNum']) {
          return;
      }
      // console.log('[SGF PLAY] SGFPlayedNodeList', SGFPlayedNodeList);
      const node = getNodeDataAtPosition(gameStatus['currentGameTree'], position);
      // board, 
      stackpush(historyBoardList, board);
      setHistoryBoardList(historyBoardList);
      // 
      if (node['B']) {
          const newBoard = board.makeMove(1, [convertTable[node['B'][0][0]], convertTable[node['B'][0][1]]]);
          SGFPlayedNodeList.push({'B': [convertTable[node['B'][0][0]], convertTable[node['B'][0][1]]]});
          setBoard(newBoard);
      }
      // 
      if (node['W']) {
          const newBoard = board.makeMove(-1, [convertTable[node['W'][0][0]], convertTable[node['W'][0][1]]]);
          SGFPlayedNodeList.push({'W': [convertTable[node['W'][0][0]], convertTable[node['W'][0][1]]]});
          setBoard(newBoard);
      }
      gameStatus['treePosition']++;
    } else if (mode === GameMode.Free || mode === GameMode.HumanVSComputer || mode === GameMode.HumanVSComputerWithFriend) {
      // un-undo, , 
      // [TODO] 
      // 
      if (lastNode == null) {
        console.log('[ERR]: ');
        return;
      }
      // gameStatus
      addNewNode(lastNode['sign'], lastNode['pos'], gameStatus);
      // lastNode
      const newBoard = futureBoardList.pop();
      if (newBoard) {
        setBoard(newBoard);
        shareData.board = newBoard;
        setFutureBoardList(futureBoardList);
        // [TODO] playgenmove
      }
      // lastNode
      setLastNode(null);
    }
  };

  const nextMove5time = () => {
    if (mode === GameMode.SGF) {
      if (checkNoSGF(gameStatus)) {
          return;
      }
      let newBoard = board;
      for (let i = 0; i < 5; i++) {
        const position = gameStatus['treePosition'];
        // 
        if (position == gameStatus['stepNum']) {
            break;
        }
        stackpush(historyBoardList, newBoard);
        const node = getNodeDataAtPosition(gameStatus['currentGameTree'], position);
        // 
        if (node['B']) {
            newBoard = newBoard.makeMove(1, [convertTable[node['B'][0][0]], convertTable[node['B'][0][1]]]);
            SGFPlayedNodeList.push({'B': [convertTable[node['B'][0][0]], convertTable[node['B'][0][1]]]});
        }
        // 
        if (node['W']) {
            newBoard = newBoard.makeMove(-1, [convertTable[node['W'][0][0]], convertTable[node['W'][0][1]]]);
            SGFPlayedNodeList.push({'W': [convertTable[node['W'][0][0]], convertTable[node['W'][0][1]]]});
        }
        gameStatus['treePosition']++;
      }
      setHistoryBoardList(historyBoardList);
      setBoard(newBoard);
    } else if (mode === GameMode.Free || mode === GameMode.HumanVSComputer || mode === GameMode.HumanVSComputerWithFriend) {
      // [TODO] 
      // un-undo, , 
      let newBoard = null;
      let lastBoard = null;
      for (let i=0; i<5; i++) {
        newBoard = futureBoardList.pop();
        if (newBoard) {
          lastBoard = newBoard;
          // [TODO] playgenmove
        } else {
          // ,
          break;
        }
      }
      if (lastBoard) {
        setBoard(lastBoard);
        shareData.board = lastBoard;
        setFutureBoardList(futureBoardList);
      }
    }
  };

  const backMove = () =>{
    if (mode === GameMode.SGF) {
      if (checkNoSGF(gameStatus)) {
        return;
      }
      console.log('backMove gameStatus: ', gameStatus);
      // 0
      if (gameStatus['treePosition'] == 0) {
        return;
      }
      // lastNode
      const lastNodeTmp = getNodeDataAtPosition(gameStatus['currentGameTree'], gameStatus['treePosition']-1);
      const tmp = parseNodeData(lastNodeTmp);
      setLastNode({sign: tmp['color'], pos: tmp['pos']});
      // gameStatus
      gameStatus['treePosition']--;
      // undo
      // historyBoardfutureBoardList;
      const historyBoard = historyBoardList.pop();
      if (!historyBoard) {
        return;
      }
      // futureBoard
      stackpush(futureBoardList, board);
      setFutureBoardList(futureBoardList);
      // , board
      setBoard(historyBoard);
      shareData.board = historyBoard;
      setHistoryBoardList(historyBoardList);
    } else
    if (mode === GameMode.HumanVSComputer || mode === GameMode.HumanVSComputerWithFriend || mode === GameMode.Free) {
      // [TODO]gameStatusid
      const lastNodeTmp = gameStatus['currentGameTree']['root']['children'].pop()['data'];
      const tmp = parseNodeData(lastNodeTmp);
      setLastNode({sign: tmp['color'], pos: tmp['pos']});

      const historyBoard = historyBoardList.pop();
      if (!historyBoard) {
        return;
      }
      // futureBoard
      stackpush(futureBoardList, board);
      setFutureBoardList(futureBoardList);
      // , board
      setBoard(historyBoard);
      shareData.board = historyBoard;
      setHistoryBoardList(historyBoardList);
      // gtp, 
      sendCommonGtp(null, 'undo');
    }
  };

  const backMove5time = () => {
    let lastBoard = null;
    if (mode === GameMode.SGF) {
      for (let i=0; i<5; i++) {
        const historyBoard = historyBoardList.pop();
        // 0,0
        if (gameStatus['treePosition'] != 0) {
          // lastNode
          const lastNodeTmp = getNodeDataAtPosition(gameStatus['currentGameTree'], gameStatus['treePosition']-1);
          const tmp = parseNodeData(lastNodeTmp);
          setLastNode({sign: tmp['color'], pos: tmp['pos']});
          // gameStatus
          gameStatus['treePosition']--;
        }
        if (historyBoard) {
          // futureBoard
          if (i==0) {
            stackpush(futureBoardList, board);
          } else {
            stackpush(futureBoardList, lastBoard);
          }
          lastBoard = historyBoard;
          // [TODO] gameStatussgfTree
        } else {
          break;
        }
      };
      if (lastBoard) {
        // 
        setHistoryBoardList(historyBoardList);
        setFutureBoardList(futureBoardList);
        shareData.board = lastBoard;
        setBoard(lastBoard);
      }
    } else
    if (mode === GameMode.HumanVSComputer || mode === GameMode.HumanVSComputerWithFriend || mode === GameMode.Free) {
      for (let i=0; i<5; i++) {
        // [TODO]gameStatusid
        // console.log(gameStatus['currentGameTree']['root']['children']);
        const lastNodeTmp = gameStatus['currentGameTree']['root']['children'].pop()['data'];
        const tmp = parseNodeData(lastNodeTmp);
        setLastNode({sign: tmp['color'], pos: tmp['pos']});
        // historyBoard
        const historyBoard = historyBoardList.pop();
        if (historyBoard) {
          // futureBoard
          if (i==0) {
            stackpush(futureBoardList, board);
          } else {
            stackpush(futureBoardList, lastBoard);
          }
          lastBoard = historyBoard;
          sendCommonGtp(null, 'undo');
        } else {
          break;
        }
      };
      if (lastBoard) {
        setHistoryBoardList(historyBoardList);
        setFutureBoardList(futureBoardList);
        shareData.board = lastBoard;
        setBoard(lastBoard);
      }
    }
  };

  // if (gobackN !==0 && gobackN !== null) {
  //   // 
  //   const reminder = gobackN % 5;
  //   const goback5 = parseInt(gobackN/5);
  //   for (let i=0; i < goback5; i++) {
  //     backMove5time();
  //   }
  //   for (let i=0; i < reminder; i++) {
  //     backMove();
  //   }
  //   dispatch({type: 'kataData/gobackN', id: null});
  // }

  const clearBoard = () => {
    windowModalComp('', ',', finishBoard, ()=>{}, '', '');
  };

  return (
    <div className="operator-panel">
      <div className="operator-card">
        <Tooltip placement="topLeft" title={t('*5/')}>
          <Button disabled={historyBoardList.length <= 0} icon={<BackwardOutlined />} onClick={backMove5time}></Button>
        </Tooltip>
        <Tooltip placement="topLeft" title={t('/')}>
          <Button disabled={historyBoardList.length <= 0} icon={<CaretLeftOutlined />} onClick={backMove}></Button>
        </Tooltip>
        <Tooltip placement="topLeft" title={t('')}>
          <Button disabled={(mode!=='SGF' && futureBoardList.length <= 0)} icon={<CaretRightOutlined />} onClick={nextMove}></Button>
        </Tooltip>
        <Tooltip placement="topLeft" title={t('*5')}>
          <Button disabled={(mode!=='SGF' && futureBoardList.length <= 0)} icon={<ForwardOutlined />} onClick={nextMove5time}></Button>
        </Tooltip>
        <Tooltip placement="topLeft" title={t('')}>
          <Button disabled={mode==='SGF' || !startGame} icon={<QuestionOutlined />} onClick={askHelp}></Button>
        </Tooltip>
        <Tooltip placement="topLeft" title={t('')}>
          <Button disabled={!startGame} icon={<ClearOutlined />} onClick={clearBoard}></Button>
        </Tooltip>
      </div>
    </div>
  );
};
