import React, {useState} from 'react';
import {Button, Select} from 'antd';
import {SearchOutlined} from '@ant-design/icons';
import {set} from '../../util/storage.js';
import {get, getResponse, getWithoutServerAddr} from '../../util/fetch';
import {clearSGFPlayedNodeList} from '../../util/sgf/fn';
import {useTranslation} from 'react-i18next';
import {translate} from '../../util/helper.js';
import './index.css';

const {Option} = Select;
const debug = false;

let consoleLog;
if (debug) {
  consoleLog = console.log;
} else {
  consoleLog = ()=>{};
}


function handlText(text) {
  let newText = '';
  let l = 0;
  let f = 0;
  for (let i = 0; i < text.length; i++) {
      if (text[f] == ';' && (text[f+1] == 'B' || text[f+1] == 'W') && text[f+2]=='[' && text[f+5]==']') {
          newText+=text.slice(l, f) + '(' + text.slice(f, f+6) + ')';
          l = f+6;
      }
      if (f+6 >= text.length) {
          newText+=text.slice(l, f+5);
          return newText;
      }
      f++;
  }
  return newText;
}

/*eslint-disable*/
const SGFSelect = React.forwardRef((props, ref) => (
  <Select
    showSearch
    style={{
      width: '80%',
      minWidth: 's250px',
    }}
    placeholder={t('SGF')}
    onSearch={props.handleSearch}
    onBlur={props.onBlur}
    open={props.open}
    autoFocus={props.autoFocus}
    // onSearch={handleChange}
    notFoundContent={null}
    suffixIcon={null}
    ref = {ref}
    searchValue={props.searchValue}
    >
    {props.candidatesList}
  </Select>
));
/* eslint-enable */

const ExternalSGF = () => {
  const {t} = useTranslation();
  const [value, setValue] = useState(undefined);
  const [searchValue, setSearchValue] = useState('');
  const [placeholder, setPlaceholder] = useState('SGF');
  const [loading, setLoading] = useState(false);
  const [open, setOpen] = useState(false);
  const [notFoundContent, setNotFoundContent] = useState(null);
  const [candidatesList, setCandidatesList] = useState([]);
  const [candidatesNameList, setCandidatesNameList] = useState([]);
  const [fromFocusFlag, setFromFocusFlag] = useState(false);
  const ref = React.createRef();

  async function fetchSGFList(keyword) {
    setNotFoundContent(null);
    setLoading(true);
    consoleLog('[SGF]fetchSGFList keyword: ', keyword);
    const url = `/qq/?keyword=${keyword}&pageSize=10&lastCode=xxxxx`;
    const resRaw = await get(url);
    const res = await getResponse(resRaw);
    const tmpCandidatesNameList = [];
    const data = res.data;
    setLoading(false);
    if (!data) {
      consoleLog('server return nothing, return in fetchSGFList');
      setNotFoundContent(t(''));
      return;
    }
    consoleLog('search res: ', data);
    setCandidatesList(data);
    for (let i = 0; i < data.length; i++) {
      tmpCandidatesNameList.push(<Option key={i}>{data[i]['Gn']}</Option>);
    }
    consoleLog('tmpCandidatesNameList: ', tmpCandidatesNameList);
    setCandidatesNameList(tmpCandidatesNameList);
    return tmpCandidatesNameList.map((value, index) => ({
      label: value,
      value: index,
    }));
  }

  // fake fetch
  /* async function fetchSGFList(keyword) {
    const tmpChild = [];
    for (let i = 10; i < 36; i++) {
      tmpChild.push(<Option key={i.toString(36) + i}>{i.toString(36) + i}</Option>);
    }
    setCandidatesNameList(tmpChild);
  } */

  async function fetchSGF(sgfUrl) {
    const resRaw = await getWithoutServerAddr(sgfUrl);
    const text = await resRaw.text();
    // console.log('sgftext', text);
    return text;
  }

  async function handleSelectOption(index, option) {
    if (!index) {
      return;
    }
    consoleLog('[SGF]select option: ', option);
    const sgfUrl = candidatesList[index]['SgfUrl'];
    const sgfTextTmp = await fetchSGF(sgfUrl);
    // gameTreesgf
    // const sgfText = handlText(sgfTextTmp);
    const sgfText = sgfTextTmp;
    consoleLog('option["children"]: ', option['children']);
    consoleLog('sgfText: ', sgfText);
    setValue(undefined);
    setSearchValue(undefined);
    setPlaceholder(option['children']);
    setOpen(false);
    // 
    // SGF
    set('sgfText', sgfText);
    // SGFPlayedNodeList
    clearSGFPlayedNodeList();
    // TODO 
  };

  async function handleClickSearch(value) {
    consoleLog('[SGF]click search: ', value);
    setCandidatesNameList([]);
    fetchSGFList(value);
    setOpen(true);
    setPlaceholder(value);
    ref.current.focus();
    setFromFocusFlag(true);
  };

  function onBlur() {
    setFromFocusFlag(true);
    consoleLog('Blur');
    setOpen(false);
    consoleLog('[SGF]Blur searchValue: ', searchValue);
    setSearchValue(searchValue);

    // console.log('setOpen: false');
  }

  async function handleInputSearch(value) {
    // setValue('');
    setValue('');
    consoleLog('[SGF]input value', value);
    setPlaceholder(undefined);
    // setPlaceholder(undefined); "SGF""undefined"
    // consoleLog('[SGF]input fromFocusFlag', fromFocusFlag);
    setOpen(false);
    if (fromFocusFlag) {
      setFromFocusFlag(false);
    } else {
      setSearchValue(value);
    }
  }

  return (
    <div className='sgf-search'>
        <Select
        showSearch
        style={{
          width: '80%',
          minWidth: 's250px',
        }}
        placeholder={placeholder=='SGF'?t('SGF'):placeholder}
        // placeholder={placeholder}
        value={value}
        onSearch={handleInputSearch}
        searchValue={searchValue}
        onChange={handleSelectOption}
        loading={loading}
        onBlur={onBlur}
        open={open}
        notFoundContent={notFoundContent}
        suffixIcon={null}
        filterOption={false}
        ref={ref}
        >
          {candidatesNameList}
      </Select>

      <Button className='search-button' shape="circle" icon={<SearchOutlined />} onClick={()=>{
        handleClickSearch(searchValue);
        }} />
    </div>
  );
};

// 
export {
  ExternalSGF,
};
