import React, {useState, useEffect} from 'react';
import {Card, Modal, Divider, Select, Radio, Button, Tooltip} from 'antd';
import {useTranslation} from 'react-i18next';
import {GameMode, aiAddr} from '../../util/constant.js';
import {ReadSGF, SaveSGF} from '../../components/sgf';
import {ExternalSGF} from '../externalSGF';
import {set, get} from '../../util/storage';
import Cookies from 'js-cookie';
import {get as getFetch, getResponse} from '../../util/fetch';
import {getSGFPlayedNodeList} from '../../util/sgf/fn';
import {windowModalComp} from '../../util/helper';
import Battle from '../../api/battle';
import {translate} from '../../util/helper';
import {useSelector} from 'react-redux';


const {askHelp, initBoardFromMap, kataAnalyze} = Battle;

import './index.css';
import {useMount} from 'ahooks';

const {Option} = Select;

const GameModeList = (props) => {
  const {t} = useTranslation();
  const {changeGameMode, mode} = props;
  const gameModeSwitch = (e) => {
    const aimMode = e.target.value;
    if (aimMode == mode) {
      return;
    }
    if (mode == GameMode['SGF']) {
      // SGF
      set('sgfText', null);
      set('gameStatus', null);
    }
    if (aimMode === GameMode['SGF']) {
      const sgfText = get('sgfText');
      // console.log('[Mode] sgf: ', sgfText);
      if (sgfText == 'null' || sgfText == false) {
        Modal.info({
          title: translate(''),
          content: translate('SGF'),
          onOk: () => {},
        });
        return;
      }
    }
    changeGameMode(GameMode[aimMode]);
  };
  // [TODO]
  // SGFgameTreegameTree,hellogameTree
  return (
    <div className='gamemode-button-list'>
      <Radio.Group value={mode} onChange={gameModeSwitch} size='medium' buttonStyle="solid">
        {/* [DEMO] 
          import {useTranslation} from 'react-i18next';
          
            const {t} = useTranslation();
            {t('keyword')} --> ;
           */}
        <Radio.Button value="HumanVSComputer">{t('')}</Radio.Button>
        <Radio.Button value="HumanVSComputerWithFriend">{t('')}</Radio.Button>
        <Radio.Button value="Free">{t('')}</Radio.Button>
        <Radio.Button value="SGF">{t('')}</Radio.Button>
      </Radio.Group>
    </div>
  );
};

const GameAISelect = () => {
  const {t} = useTranslation();
  const [optionList, setOptionList] = useState([]);
  const [nowAIText, setNowAIText] = useState('AI:');
  useMount(async ()=>{
    const nowAI = await Cookies.get('aiId');
    const resRaw = await getFetch(aiAddr);
    const res = await getResponse(resRaw);
    const {data=[]} = res || {};
    const tmpOptionList = [];
    data.forEach((item, ind)=>{
      tmpOptionList.push(<Option value={item['UserId']} key={'ai_'+item['UserId']+ind}>{`${item['Name']}|:${item['Online']}|:${item['Analyze']}|:${item['Genmove']}|:${item['Maxplayer']}|Vip:${item['Vip']}`}</Option>);
    });
    setOptionList(tmpOptionList);
    setNowAIText('AI:' + nowAI);
  });
  const changeAI = (chooseAI) => {
    Cookies.set('aiId', chooseAI);
    setNowAIText('AI:' + chooseAI);
    windowModalComp('', 'AI: '+ chooseAI);
  };

  return (
    <div>
      <span>{nowAIText||translate('AI')}</span>
      <Select onChange={changeAI} placeholder={t('AI')} className="choose-ai-select">
        {optionList}
      </Select>
    </div>
  );
};

export default function LeftPanel(props) {
  // const kataDataStatus = useSelector((state) => state.kataData.kataDataStatus);
  const {t} = useTranslation();
  const {changeGameMode, mode, ws, startGame} = props;
  // 
  const [gameAnalysis, setGameAnalysis] = useState(false);

  useEffect(() => {
    Cookies.set('gameAnalysis', gameAnalysis);
  }, [gameAnalysis]);

  const sgfToFree = () => {
      const nodeList = getSGFPlayedNodeList();
      initBoardFromMap(nodeList, ws);
      changeGameMode('Free');
  };

  const changeGameAnalysis = () => {
    kataAnalyze(ws, gameAnalysis);
    setGameAnalysis(!gameAnalysis);
  };

  return (
    <div className='left-panel'>
      <Card className="gamemode-card" title={t('')}>
        <GameModeList changeGameMode={changeGameMode} mode={mode} />
        {mode !== 'Free' && <Divider />}
        {mode === 'HumanVSComputer' && <div>
          <GameAISelect />
          <Divider></Divider>
          <Button disabled={startGame} onClick={changeGameAnalysis}>{t('AI')}:{gameAnalysis ? t('') : t('')}</Button>
          <Button className='ask-help-btn' onClick={askHelp}>{t('')}</Button>
        </div>
        }
        {
          mode === 'HumanVSComputerWithFriend' &&
          <GameAISelect />
        }
        {
          mode === 'SGF' &&
          <Tooltip placement="topLeft" title=''>
            <Button onClick={sgfToFree}>{t('')}</Button>
          </Tooltip>
        }
      </Card>
      <Card className="search-card" title={t('SGF')}>
        <div className="search-card-container"><ExternalSGF /></div>
      </Card>
      <Card title={t('(SGF)')} className="sgf-card">
        <Card.Grid style={{width: '50%', textAlign: 'center'}}><ReadSGF /></Card.Grid>
        <Card.Grid style={{width: '50%', textAlign: 'center'}}><SaveSGF /></Card.Grid>
      </Card>
    </div>
  );
};
