import {NavLink} from 'react-router-dom';
import './style.css';
import Cookies from 'js-cookie';
import {Menu, Dropdown, Button} from 'antd';
import {DownOutlined} from '@ant-design/icons';
import {useTranslation} from 'react-i18next';


const Header = () => {
  const {t, i18n} = useTranslation();

  const changeLanguage = (lng) => {
    i18n.changeLanguage(lng);
  };

  const loginStatus = Cookies.get('auth');

  const menu = (
    <Menu style={{background: '#000'}} className='lang-menu' color="#000">
      <Menu.Item key="0">
        <Button type='text' onClick = {()=>{
          changeLanguage('zhCn');
        }}>
          
        </Button>
      </Menu.Item>
      <Menu.Item key="1">
        <Button type='text' onClick = {()=>{
          changeLanguage('zhTc');
        }}>
          
        </Button>
      </Menu.Item>
      <Menu.Item key="2">
        <Button type='text' onClick = {()=>{
          changeLanguage('ko');
        }}>
          
        </Button>
      </Menu.Item>
      <Menu.Item key="3">
        <Button type='text' onClick = {()=>{
          changeLanguage('en');
        }}>
          English
        </Button>
      </Menu.Item>
      <Menu.Item key="5">
        <Button type='text' onClick = {()=>{
          changeLanguage('ja');
        }}>
          
        </Button>
      </Menu.Item>
    </Menu>
  );

  return (
    <header className='header'>
      <h1>{t('Katago')}</h1>
      <nav>
        {/* [TODO] LinkclassNameactive */}
        <NavLink to="/home">{t('')}</NavLink>
        {!loginStatus && <NavLink to="/login">{t('')}</NavLink>}
        {loginStatus && <NavLink to="/login" onClick={()=>{
          Cookies.set('auth', null);
          Cookies.set('userName', null);
          Cookies.set('userId', null);
        }}>{t('')}</NavLink>}
        {/* <NavLink to="/registry"></NavLink> */}
        {/*  */}
        <Dropdown overlay={menu}>
          <a className="ant-dropdown-link" onClick={(e) => e.preventDefault()}>
            {t('')} <DownOutlined />
          </a>
      </Dropdown>
      </nav>
    </header>
  );
};


export {
  Header,
};
