import react from 'react';
import helper from './helper';

const {createElement: h, Component} = react;
const {vertexEquals} = helper;

class Line extends Component {
  shouldComponentUpdate(nextProps) {
    const {v1, v2, type, vertexSize} = this.props;

    return (
      type !== nextProps.type ||
      vertexSize !== nextProps.vertexSize ||
      !vertexEquals(v1, nextProps.v1) ||
      !vertexEquals(v2, nextProps.v2)
    );
  }

  render() {
    const {v1, v2, type = 'line', vertexSize} = this.props;
    if (vertexEquals(v1, v2)) return;

    const [pos1, pos2] = [v1, v2].map((v) => v.map((x) => x * vertexSize));
    const [dx, dy] = pos1.map((x, i) => pos2[i] - x);
    const [left, top] = pos1.map((x, i) => (x + pos2[i] + vertexSize) / 2);

    const angle = (Math.atan2(dy, dx) * 180) / Math.PI;
    const length = Math.sqrt(dx * dx + dy * dy);

    return h('div', {
      className: `shudan-${type}`,
      style: {
        position: 'absolute',
        left,
        top,
        margin: 0,
        width: length,
        transform: `translateX(${-length / 2}px) rotate(${angle}deg)`,
      },
    });
  }
}

export default Line;
// module.exports = Line;
