/*
  Tree
*/
export function addNewNode(sign, vertex, gameStatus) {
  const color = sign == 1 ? 'B': 'W';
  // gameStatus.currentGameTree.merger(
  //   gameStatus.currentGameTree.get(gameStatus.treePosition),
  //   {[color]: [sgf.stringifyVertex(vertex)]});
  let id;
  console.log('[*] gamestatus before addNewNode', gameStatus);
  console.log('[*] gamestatus before addNewNode', JSON.stringify(gameStatus));
  console.log('newTree.getHeight before: ', gameStatus['currentGameTree'].getHeight());
  let newTree;
  // id=0
  // console.log('gameStatus[\'currentGameTree\']: ', JSON.stringify(gameStatus['currentGameTree']));
  // console.log('gameStatus[\'currentGameTree\'][\'children\']: ', JSON.stringify(gameStatus['currentGameTree']['children']));
  // console.log('gameStatus[\'currentGameTree\'][\'id\']: ', JSON.stringify(gameStatus['currentGameTree']['id']));
  // console.log('gameStatus[\'currentGameTree\'][\'data\']: ', JSON.stringify(gameStatus['currentGameTree']['data']));
  // console.log('gameStatus[\'currentGameTree\'][\'parentId\']: ', JSON.stringify(gameStatus['currentGameTree']['parentId']));
  // console.log('gameStatus[\'currentGameTree\'][\'data\']==[]: ', gameStatus['currentGameTree']['children']==undefined);
  // if (gameStatus['currentGameTree']['children']==undefined) {
  // if (gameStatus.treePosition==0 && gameStatus['currentGameTree']['children']==undefined) {
    if (gameStatus.stepNum==0) {
    newTree = gameStatus.currentGameTree.mutate((draft) => {
      draft.addToProperty(draft.root.id,
        color, sgf.stringifyVertex(vertex));
    });
    id = 0;
  } else {
    // console.log('[*] vertex in addNewNode', vertex);
    newTree = gameStatus.currentGameTree.mutate((draft) => {
      id = draft.appendNode(gameStatus.treePosition,
        {[color]: [sgf.stringifyVertex(vertex)]});
    });
  }
  gameStatus.currentGameTree = newTree;
  gameStatus.treePosition = id;
  gameStatus.stepNum ++;
  // gameStatus
  set('gameStatus', gameStatus);
  return gameStatus;
  // console.log('[*] gamestatus after addNewNode', JSON.stringify(gameStatus));
  // console.log('[*] gamestatus after addNewNode', getStringChild(gameStatus));
}

// gameStatus
export function initGameStatus(emptyTree) {
  emptyTree['data'] = {CP: ['hello']};
  return emptyTree;
}

export function readSGFTree(gameStatus) {
  const content = get('sgfText') || '';
  if (content === '') {
    windowModalComp('', '');
    return;
  }
  const lastId = getTotalNodesNum(content)-1;
  const getId = ((id) => () => id++)(lastId);
  const rootNodes = sgf.parse(content, getId);
  const gameTrees = rootNodes.map((rootNode) => {
      return new GameTree({getId, root: rootNode});
  });
  // console.log('[SGF] json1 gameTrees: ', JSON.stringify(gameTrees));
  gameStatus['currentGameTree'] = gameTrees;
  gameStatus['sgf'] = content;
  gameStatus['treePosition'] = 0;
  gameStatus['stepNum'] = lastId+1;
  // console.log('[SGF]] gameStatus: ', gameStatus);
}

function getTotalNodesNum(content) {
  let totalNodeNum = 0;
  let reg = new RegExp(';B\\[', 'g');
  totalNodeNum += content.match(reg).length;
  reg = new RegExp(';W\\[', 'g');
  totalNodeNum += content.match(reg).length;
  return totalNodeNum;
}


export function getStringChild(gameStatus) {
  return JSON.stringify(gameStatus['currentGameTree']['root']['children']);
}

// (W: ["cd"])
export function parseNodeData(Node) {
  let color;
  let posStr;
  const pos = [];
  if (Node['W']) {
      color = -1;
      posStr = Node['W'];
  } else {
      color = 1;
      posStr = Node['B'];
  }
  pos.push(convertTable[posStr[0]]);
  pos.push(convertTable[posStr[1]]);
  return {color: color, pos: pos};
}

