/**
 * kata-analyze
 * @param {*} jsonData  ws
 * @param {*} shareData 
 * @return {Boolean}    
 */
const parseAnalysis = (jsonData, shareData) => {
  const msg = jsonData.body.msg;
  const gtpInfo = gtpParse(msg);
  if (gtpInfo && gtpInfo.name === 'info') {
    const infoData = gtpInfo.args;
    let indicator = '';
    const indicatorList = ['info', 'move', 'visits', 'winrate', 'scoreMean', 'scoreStdev', 'scoreLead', 'scoreSelfplay', 'prior', 'lcb', 'utilityLcb', 'order', 'pv'];
    const infoList = [];
    let eachInfo = {pv: []};
    infoData.forEach((item) => {
      let isIndicator = false;
      indicatorList.forEach((ind) => {
        if (isIndicator) {
            return;
        }
        if (item === ind) {
          isIndicator = true;
          indicator = ind;
        }
      });
      if (!isIndicator || indicator === 'info') {
        // , indicator
        if (indicator === 'info') {
          // ,, , 
          if (eachInfo.move) {
            infoList.push(eachInfo);
          }
          // eachInfo, 
          eachInfo = {pv: []};
        } else if (indicator === 'pv') {
          // pv
          eachInfo.pv.push(item);
        } else {
          eachInfo[indicator] = item;
        }
      }
    });
    // push, info,push
    infoList.push(eachInfo);
    // console.log('[*] :', infoList);
    shareData.dispatch({type: 'kataData/addData', kataData: infoList});
    // shareData.kataData && shareData.kataData.push(infoList);
    return false;
  }
  return false;
};

