import React, {useEffect, useState} from 'react';
import {fetchLineData} from '../../api/mock';

import {Card} from 'antd';
// import {Line} from '@ant-design/charts';
import {useTranslation} from 'react-i18next';
import {useSelector} from 'react-redux';

import NodeTreeGraph from '../nodeTreeGraph';


import './index.css';

const reconstruct = (obj) => {
  if (!obj) {
    return {};
  }
  try {
    if (Object.prototype.toString.call(obj) !== '[object Object]') {
      obj = JSON.parse(obj);
    }
  } catch (e) {
    console.log('error parse json object:', e);
    console.log(Object.prototype.toString.call(obj), obj);
  }
  if (!obj) {
    return {};
  }
  if (obj.data.B) {
    obj.B = obj.data.B;
  } else if (obj.data.W) {
    obj.W = obj.data.W;
  }
  obj.data = null;
  obj.id = String(obj.id);
  if (obj.children && Array.isArray(obj.children)) {
    obj.children.forEach((item) => {
      reconstruct(item);
    });
  }
  return obj;
};

export default function RightPanel() {
  const {t} = useTranslation();
  const [lintData, setLineData] = useState(null);
  const [lineConfig, setLineConfig] = useState([]);

  const kataData = useSelector((state) => state.kataData.kataData);
  const gameTreeNode = useSelector((state) => state.kataData.gameStatus);

  useEffect(() => {
    const data = fetchLineData();
    const lineConfig = {
        width: 300,
        height: 150,
        xField: 'time',
        yField: 'possibility',
        render: 'canvas',
        autoFit: true,
        point: {
        size: 5,
          shape: 'diamond',
        },
        label: {
          style: {
            fill: '#aaa',
          },
        },
    };
    setLineConfig(lineConfig);
    setLineData(data);
  }, []);
  const treeData = reconstruct(gameTreeNode);
  return (
  <div className='right-panel'>
    {/* <div className="code">{JSON.stringify(gameTreeNode)}</div> */}
    <Card className="tree-card" title={t('')}>
      <div className='tree-canvas'>
        {JSON.stringify(treeData) !== '{}' && <NodeTreeGraph data={treeData} width={400} height={400} />}
      </div>
    </Card>
    {/* <div className='line-canvas'>
      {lintData && <Card className="win-rate-card" title={t('')}><Line {...lineConfig} data={lintData} /></Card>}
    </div> */}
  </div>);
};
