import {createTokenizer, regexRule} from 'doken';
import iconv from './iconv-lite';
import jschardet from './jschardet';
import helper from './helper';
const {unescapeString} = helper;

const encodingDetectionProps = [
  'EV',
  'GN',
  'GC',
  'AN',
  'BT',
  'WT',
  'PW',
  'PB',
  'C',
];

const _tokenize = createTokenizer({
  rules: [
    regexRule('_whitespace', /\s+/y, {lineBreaks: true}),
    regexRule('parenthesis', /(\(|\))/y),
    regexRule('semicolon', /;/y),
    regexRule('prop_ident', /[A-Za-z]+/y),
    regexRule('c_value_type', /\[([^\\\]]|\\[^])*\]/y, {lineBreaks: true}),
    {
      type: 'invalid',
      match: (input, position) => ({length: 1}),
    },
  ],
});

const tokenizeIter = function* (contents) {
  for (const token of _tokenize(contents)) {
    token.progress = token.pos / (contents.length - 1);
    delete token.length;

    yield token;
  }
};

const tokenizeBufferIter = function* (buffer, {encoding = null} = {}) {
  if (encoding != null) {
    yield* tokenizeIter(iconv.decode(buffer, encoding));
    return;
  }

  // Guess encoding

  const detectedEncoding = jschardet.detect(buffer.slice(0, 300)).encoding;
  const contents = iconv.decode(buffer, detectedEncoding);
  const tokens = tokenizeIter(contents);

  // Search for encoding

  const prelude = [];

  while (true) {
    const next = tokens.next();
    if (next.done) break;

    const {type, value} = next.value;
    const lastToken = prelude[prelude.length - 1];

    prelude.push(next.value);

    if (
      type === 'c_value_type' &&
      lastToken != null &&
      lastToken.type === 'prop_ident' &&
      lastToken.value === 'CA'
    ) {
      encoding = unescapeString(value.slice(1, -1));
      break;
    }
  }

  if (
    encoding != null &&
    encoding != detectedEncoding &&
    iconv.encodingExists(encoding)
  ) {
    yield* tokenizeIter(iconv.decode(buffer, encoding));
  } else {
    yield* prelude;
    yield* tokens;
  }
};

const tokenize = (contents) => [...tokenizeIter(contents)];

const tokenizeBuffer = (buffer, opts) => [
  ...tokenizeBufferIter(buffer, opts),
];

const token = {
  encodingDetectionProps,
  tokenizeIter,
  tokenizeBufferIter,
  tokenize,
  tokenizeBuffer,
};

export default token;
