const alpha = 'ABCDEFGHJKLMNOPQRSTUVWXYZ';
const vertexEvents = [
  'Click',
  'MouseDown',
  'MouseUp',
  'MouseMove',
  'MouseEnter',
  'MouseLeave',
];

const range = function(n) {
  return Array(n).fill(0).map((_, i) => i);
};
const random = (n) => Math.floor(Math.random() * (n + 1));
const neighborhood = ([x, y]) => [
  [x, y],
  [x - 1, y],
  [x + 1, y],
  [x, y - 1],
  [x, y + 1],
];
const vertexEquals = ([x1, y1], [x2, y2]) => x1 === x2 && y1 === y2;
const lineEquals = ([v1, w1], [v2, w2]) =>
  vertexEquals(v1, v2) && vertexEquals(w1, w2);

const getHoshis = function(width, height) {
  if (Math.min(width, height) <= 6) return [];

  const [nearX, nearY] = [width, height].map((x) => (x >= 13 ? 3 : 2));
  const [farX, farY] = [width - nearX - 1, height - nearY - 1];
  const [middleX, middleY] = [width, height].map((x) => (x - 1) / 2);

  const result = [
    [nearX, farY],
    [farX, nearY],
    [farX, farY],
    [nearX, nearY],
  ];

  if (width % 2 !== 0 && height % 2 !== 0 && width !== 7 && height !== 7) {
result.push([middleX, middleY]);
}
  if (width % 2 !== 0 && width !== 7) {
result.push([middleX, nearY], [middleX, farY]);
}
  if (height % 2 !== 0 && height !== 7) {
result.push([nearX, middleY], [farX, middleY]);
}

  return result;
};

const readjustShifts = function(shiftMap, vertex = null) {
  if (vertex == null) {
    for (let y = 0; y < shiftMap.length; y++) {
      for (let x = 0; x < shiftMap[0].length; x++) {
        readjustShifts(shiftMap, [x, y]);
      }
    }
  } else {
    const [x, y] = vertex;
    const direction = shiftMap[y][x];

    const data = [
      // Left
      [
        [1, 5, 8],
        [x - 1, y],
        [3, 7, 6],
      ],
      // Top
      [
        [2, 5, 6],
        [x, y - 1],
        [4, 7, 8],
      ],
      // Right
      [
        [3, 7, 6],
        [x + 1, y],
        [1, 5, 8],
      ],
      // Bottom
      [
        [4, 7, 8],
        [x, y + 1],
        [2, 5, 6],
      ],
    ];

    for (const [directions, [qx, qy], removeShifts] of data) {
      if (!directions.includes(direction)) continue;

      if (shiftMap[qy] && removeShifts.includes(shiftMap[qy][qx])) {
        shiftMap[qy][qx] = 0;
      }
    }
  }

  return shiftMap;
};

const diffSignMap = function(before, after) {
  if (
    before === after ||
    before.length === 0 ||
    before.length !== after.length ||
    before[0].length !== after[0].length
  ) {
    return [];
  }

  const result = [];

  for (let y = 0; y < before.length; y++) {
    for (let x = 0; x < before[0].length; x++) {
      if (before[y][x] === 0 && after[y] != null && after[y][x]) {
        result.push([x, y]);
      }
    }
  }

  return result;
};

const helper = {
  alpha,
  vertexEvents,
  vertexEquals,
  range,
  random,
  neighborhood,
  lineEquals,
  getHoshis,
  readjustShifts,
  diffSignMap,
};

export default helper;
