export function aiPlay(ws, board) {
  // mock AI, 
  const map = board.signMap;
  const emptyPos = [];
  map.forEach((row, num)=>{
    row.forEach((item, letter)=>{
        if (item===0) {
          emptyPos.push([num, letter]);
        }
    });
  });
  if (emptyPos.length === 0) {
    // , 
    return {
      errno: 1000,
      errmsg: '',
    };
  }
  const r = parseInt(Math.random()*emptyPos.length);
  return {errno: 0, y: emptyPos[r][0], x: emptyPos[r][1]};
};

export function fetchLineData() {
  const data = [
    {time: 0, possibility: 0},
    {time: 1, possibility: 0.24},
    {time: 2, possibility: 0.34},
    {time: 3, possibility: 0.54},
    {time: 4, possibility: 0.84},
    {time: 5, possibility: 0.94},
  ];
  return data;
}
