import React, {useEffect, useState} from 'react';
import {login, checkAuth} from '../../api/user';
import {useMount} from 'ahooks';
import Cookies from 'js-cookie';
import {useHistory} from 'react-router-dom';
import {registry} from '../../api/user';
import {useTranslation} from 'react-i18next';
import {translate} from '../../util/helper';


import './index.css';


import {Modal} from 'antd';

function errorModal(title, content) {
  const tTitle = translate(title);
  const tContent = translate(content);
  // console.log(tTitle, tContent);
  Modal.error({
    title: tTitle,
    content: tContent,
  });
}

const layout = {
  labelCol: {
    span: 8,
  },
  wrapperCol: {
    span: 16,
  },
};
const tailLayout = {
  wrapperCol: {
    offset: 8,
    span: 16,
  },
};


const LoginSignup = () => {
  const {t} = useTranslation();
  const history = useHistory();
  const [name, setName] = useState(null);
  const [password, setPassword] = useState(null);
  const [repeatPassword, setRepeatPassword] = useState(null);
  const [loginInLeftPanel, setLoginInLeftPanel] = useState(true);

  useMount(async () => {
    const auth = Cookies.get('auth');
    // const res = await checkAuth();
    // if (res.success) {
    //   // 
    //   history.push('/home');
    // }
  });
  const signin = async () => {
    const res = await login(name, password);
    if (res.success) {
      const {data} = res;
      Cookies.set('auth', data.AuthToken);
      Cookies.set('userName', data.UserName);
      Cookies.set('userId', data.UserId);
      console.log('[Router] loginhome');
      history.push('/home');
    } else {
      console.log('[*]', res);
      errorModal('', '');
    }
  };

  const signUp = async () => {
    if ((repeatPassword !== password) || (repeatPassword == null)) {
      return;
    }

    const res = await registry(name, password);
    if (res.success) {
      const {data} = res;
      Cookies.set('auth', data.AuthToken);
      Cookies.set('userName', data.UserName);
      Cookies.set('userId', data.UserId);
      console.log('[Router] registrylogin');
      setLoginInLeftPanel(true);
    } else {
      // TODO 
      errorModal('', ',');
    }
  };

  function changePanel() {
    setLoginInLeftPanel(!loginInLeftPanel);
    setName(null);
    setPassword(null);
    setRepeatPassword(null);
  }


  return (


    <div className="login-container" id="container">
      {loginInLeftPanel &&
        <div className='login'>
          <div className="form-container">
            <div className="sign-in-container">
              <h1 className = "signin-h1">{t('')}</h1>
              <input type="text" placeholder={t('')} onChange={(e) => setName(e.target.value)} />
              <input type="password" placeholder={t('')} onChange={(e) => setPassword(e.target.value)}/>
              <a className="forget-password" href="#">{t('')}?</a>
              <button className="login-button" onClick={signin}>{t('')}</button>
            </div>
            <div className="overlay-container">
              <div className="overlay">
                <div className="overlay-panel overlay-right">
                  <h1 className="login-h1">{t('Katago')}</h1>
                  <p>{t('katago')}</p>
                  <button className="login-button" id="signUp" onClick = {changePanel}>{t('')}</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      }
      { (!loginInLeftPanel) &&
        <div className='signUp'>
          <div className="form-container">
            <div className="sign-in-container">
              <h1 className = "signin-h1">{t('')}</h1>
              <input type="text" placeholder={t('')} onChange={(e) => setName(e.target.value)} />
              <input type="password" placeholder={t('')} onChange={(e) => setPassword(e.target.value)}/>
              <input type="password" placeholder={t('')} onChange={(e) => setRepeatPassword(e.target.value)}/>
              <button className="login-button" onClick={signUp}>{t('')}</button>
            </div>
            <div className="overlay-container">
              <div className="overlay">
                <div className="overlay-panel overlay-right">
                  <h1 className="login-h1">{t('Katago')}</h1>
                  <p>{t('katago')}</p>
                  <button className="login-button" id="signUp" onClick = {changePanel}>{t('')}</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      }
    </div>

  );
};
export default LoginSignup;
