import React, {useEffect, useState} from 'react';
import './index.css';
import {registry} from '../../api/user';
import Cookies from 'js-cookie';
import {useHistory} from 'react-router-dom';

import {Form, Input, Button, Modal} from 'antd';

const layout = {
  labelCol: {
    span: 8,
  },
  wrapperCol: {
    span: 16,
  },
};
const tailLayout = {
  wrapperCol: {
    offset: 8,
    span: 16,
  },
};

function errorModal(title, content) {
  Modal.error({
    title,
    content,
  });
}

const Registry = (props) => {
  const history = useHistory();
  const [name, setName] = useState(null);
  const [password, setPassword] = useState(null);
  const [repeatPassword, setRepeatPassword] = useState(null);

  const registryAccount = async () => {
    if (repeatPassword !== password) {
      return;
    }

    const res = await registry(name, password);
    if (res.success) {
      const {data} = res;
      Cookies.set('auth', data.AuthToken);
      Cookies.set('userName', data.UserName);
      Cookies.set('userId', data.UserId);
      console.log('[Router] registrylogin');
      history.push('/login');
    } else {
      errorModal('', ',.');
    }
  };

  return (
    <div className="registry-container">
      <Form
        {...layout}
        onFinish={registryAccount}
        onFinishFailed={registryAccount}
        className="registry-form"
      >
        <Form.Item
          label=""
          name=""
          rules={[
            {
              required: true,
              message: '',
            },
          ]}
          onChange={(e) => setName(e.target.value)}
        >
          <Input />
        </Form.Item>

        <Form.Item
          label=""
          name=""
          rules={[
            {
              required: true,
              message: '',
            },
          ]}
          onChange={(e) => setPassword(e.target.value)}
        >
          <Input.Password />
        </Form.Item>

        <Form.Item
          label=""
          name=""
          rules={[
            {
              required: true,
              message: '',
            },
          ]}
          onChange={(e) => setRepeatPassword(e.target.value)}
        >
          <Input.Password />
        </Form.Item>

        <Form.Item {...tailLayout}>
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};
export default Registry;
