const {
  ControllerStateTracker,
  Controller,
  Command,
  Response,
} = require('@sabaki/gtp');

import {gtpPath} from './constant';

/*
  gtp
------------------------------------------------------------------------
  19*19,A-T,1-19,,D5
  gtp{id:int, name: '', arg: []},gtp
  gtp[id] name [arg],idarg
  gtp=,gtp?
------------------------------------gtp------------------------------------
  GTP: boardsize,clear_board,play,undo,genmove,showboard,quit
  : time_settings,time_left
  : final_score,final_status_list
  : load_sgf

--------------------------------gtp----------------------------------
  >> 1 boardsize 7   #,7*7#
  >> =1              ##
  >> 2 clear_board   ##
  >> =2              ##
  >> 3 play black D5 #D5#
  >> =3
  >> 4 genmove white ##
  >> =4 C3           ##
  >> 5 play black C3 #C3#
  >> ?5 illegal move #,,=#
  >> 6 play black E3 #E3#
  >> 7 showboard     ##
  >> =7
       A B C D E F G
     7 . . . . . . . 7
     6 . . . . . . . 6
     5 . . + X + . . 5
     4 . . . + . . . 4
     3 . . O . X . . 3
     2 . . . . . . . 2     WHITE (O) has captured 0 stones
     1 . . . . . . . 1     BLACK (X) has captured 0 stones
       A B C D E F G
  >> 8 quit          ##
  >> =8
----------------------------------------------------------------------------
  GTP-2https://www.lysator.liu.se/~gunnar/gtp/gtp2-spec-draft2.pdf
 */

// gtp
export default class GtpSocket {
  constructor(path = gtpPath) {
    this.socket = new Controller(gtpPath, ['--gtp', '--noponder']);
    this.socket.start();
    this.tracker = new ControllerStateTracker(this.socket);
  }
  /**
   * GTP socket
   * @param  {object} gtpCommand gtp {id:1, name: '', arg: []}
   * @param  {object} option      {stringifyRes: false}
   * @return {object} res        gtp response{id:1, content:'', error:false}
   */
  async send(gtpCommand, option={}) {
    const res = await this.socket.sendCommand(gtpCommand);
    if (option.stringifyRes) {
      res = Response.toString(res);
    }
    return res;
  }
  // GTP socket
  async stop() {
    try {
      await this.socket.stop();
    } catch (e) {
      return new Error(e);
    }
    return true;
  }
  async start(path) {
    if (!path) {
      return false;
    }
    this.socket = new Controller(gtpPath, ['--gtp', '--noponder']);
    return true;
  }
  /**
   * 
   * @return {object} 
   */
  async getEngineState() {
    const state = await this.tracker.state();
    return state;
  }
  async isBusy() {
    const state = await this.tracker.syncing();
    return state;
  }
  async isRightCommand(name) {
    const res = await this.tracker.knowsCommand(name);
    return res;
  }
};

/**
 * gtp
 * @param  {object} gtpParam   gtp command
 * @param  {object} option      {stringifyRes: false}
 * @return {object} res        gtp Response
 */
export async function gtpFetch(gtpParam={}, option={}) {
  const leela = new Controller(gtpPath, ['--gtp', '--noponder']);
  leela.start();
  let res = await leela.sendCommand(gtpParam);
  if (option.stringifyRes) {
    res = Response.toString(res);
  }
  await leela.stop();
  return res;
}

/**
 * gtpsting
 * @param  {object} command gtp {id:1, name: '', arg: []}
 * @return {string} res     gtpstring
 */
export function gtpStringify(command) {
  return Command.toString(command);
};

/**
 * gtp
 * @param  {string} gtpString gtp
 * @return {object} res       gtp {id:1, name: '', arg: []}
 */
export function gtpParse(gtpString) {
  return Command.fromString(gtpString);
};
