import React, {useState, useEffect} from 'react';
import {useMount} from 'ahooks';
import {useSelector, useDispatch} from 'react-redux';

import Goban from '../goboard/Goban';
import Board from '@sabaki/go-board';
import '../../assets/css/goban.css';

import {windowModalComp} from '../../util/helper';

import {Alert} from 'antd';
import {GameMode} from '../../util/constant.js';
import {set} from '../../util/storage';
import Battle from '../../api/battle';

import GameTree from '@sabaki/immutable-gametree';
import {addNewNode, clearSGFPlayedNodeList, initGameStatus, readSGFTree} from '../../util/sgf/fn';
import OperatorPanel from './operatorPanel.jsx';

import Cookies from 'js-cookie';
import {Spin} from 'antd';

import './index.css';


const {aiPlay, sendCommonGtp, finishBoard} = Battle;

const isLegal = function(board, color, pos) {
  // 
  if (!board || !(board instanceof Board)) {
    return false;
  }
  if (color!==1 && color!==-1) {
    return false;
  }
  if (!Array.isArray(pos) || pos.length !== 2) {
    return false;
  }
  const x = pos[0];
  const y = pos[1];
  // 
  if (board.get([x, y]) !== 0) {
    console.log('[#] ,');
    return false;
  }
  return true;
};

// ,ws
const shareData = {kataData: []};

export default function CustomGobangBoard(props) {
	const {signMap, showCoordinates, fuzzyStonePlacement, mode, ws, uploadState} = props;
  const [loading, setLoading] = useState(true); // 
	const [board, setBoard] = useState(new Board(signMap)); // 
  const [bindMessage, setBindMessage] = useState(false); // ws
  // const [emptyTree, setEmptyTree] = useState(new GameTree()); // 
  const [gameStatus, setGameStatus] = useState({}); // 
  const [startGame, setStartGame] = useState(false); // 
  const [message, setMessage] = useState(''); // 
  const [historyBoardList, setHistoryBoardList] = useState([]); // 
  const [futureBoardList, setFutureBoardList] = useState([]); // 
  const [lastNode, setLastNode] = useState(null);// {sign: sign, pos: [x, y]}

  const dispatch = useDispatch();
  // let gameStatus = {};// 
  const emptyTree = new GameTree();

  const clearBoard = (board, setBoard, gameStatus, setGameStatus, sendCommonGtp) => {
    // , gtp
    Cookies.set('gtpHistory', '');
    Cookies.set('gtpId', 1);
    board.clear();
    setBoard(board);
    sendCommonGtp(null, 'clear_board');
    setStartGame(false);
    const newGameStatus = {
      currentGameTree: initGameStatus(emptyTree),
      treePosition: emptyTree.root.id,
      sgf: null,
      nodeArray: null,
      stepNum: 0,
    };
    setGameStatus(newGameStatus);
    set('gameStatus', null);
    set('emptyTree', null);
    clearSGFPlayedNodeList();
    dispatch({type: 'kataData/gameStatus',
      gameStatus: JSON.stringify({
        ...newGameStatus.currentGameTree.root,
      }),
    });
  };

  useMount(() => {
    console.log('[*] ');
    shareData.addNewNode = addNewNode;
    set('gameStatus', null);
    set('emptyTree', null);
    const newGameStatus = {
      currentGameTree: initGameStatus(emptyTree),
      treePosition: emptyTree.root.id,
      sgf: null,
      nodeArray: null,
      stepNum: 0,
    };
    setGameStatus(newGameStatus);
    // {"id":0,"data":{},"parentId":null,"children":[]},"treePosition":0,"sgf":null,"nodeArray":null,"stepNum":0}
    dispatch({type: 'kataData/gameStatus',
      gameStatus: JSON.stringify({
        ...newGameStatus.currentGameTree.root,
      }),
    });
  });

  // ws, shareData
  useEffect(() => {
    if (ws && !bindMessage) {
      shareData.dispatch = dispatch; // wsredux
      shareData.board = board;
      shareData.setBoard = setBoard;
      shareData.setLoading = setLoading;
      shareData.showAlert = showAlert;
      shareData.gameStatus = gameStatus;
      shareData.setGameStatus = setGameStatus;
      shareData.clearBoard = clearBoard;
      shareData.windowModalComp = windowModalComp;
      shareData.setStartGame = setStartGame;
      shareData.initBoardStatus = false;
      ws.removeEventListener('message', Battle.defineMessage(shareData));
      ws.addEventListener('message', Battle.defineMessage(shareData));
      setBindMessage(true);
      setLoading(false);
    }
  }, [ws]);

  useEffect(() => {
    if (mode === GameMode['SGF']) {
      readSGFTree(gameStatus);
      finishBoard();
    }
    if (mode === GameMode['HumanVSComputer']) {
      finishBoard();
    }
    if (mode === GameMode['HumanVSComputerWithFriend']) {
      finishBoard();
    }
  }, [mode]);

  // state,
  // ,,
  // useEffect(() => {
  //   uploadState({startGame}, {setBoard});
  // }, []);

  const showAlert = (text, time = 1000) => {
    setMessage(text);
    setTimeout(()=>{
      setMessage('');
    }, time);
  };

  const vertexEvent = (evt, [x, y]) => {
    // ,
    if (loading) {
      return;
    }
    if (mode === 'SGF') {
      return;
    }
    // [x, y]signMap,signMap[y][x]
    // (0,0),(18,18)
    // console.log('[#] :', [x, y]);
    const sign = evt.button === 0 ? 1 : -1; // 
    // 
    if (!isLegal(board, sign, [x, y])) {
      // console.log('[#] ');
      showAlert('');
      return false;
    }
    // Tree
    addNewNode(sign, [x, y], gameStatus);
    dispatch({type: 'kataData/gameStatus', gameStatus: JSON.stringify({
      ...gameStatus.currentGameTree.root,
    })});

    const newBoard = board.makeMove(sign, [x, y]);
    if (newBoard.get([x, y]) !== sign) {
      // ,
      showAlert('');
      return false;
    }

    // , shareData!ws
		setBoard(newBoard);
    shareData.board = newBoard;
    historyBoardList.push(board); // board,board
    // gameStatus
    shareData.gameStatus = gameStatus;
		// ,
		if (mode === GameMode.HumanVSComputer || mode === GameMode.HumanVSComputerWithFriend || mode === GameMode.Free) {
      // 
      Cookies.set('playOperation', JSON.stringify({sign, pos: [x, y]}));
      // loadingws
      setLoading(true);
      if (!startGame) {
        // , 
        shareData.initBoardStatus = true;
        sendCommonGtp(ws, 'name');
        setStartGame(true);
      } else {
        // 
        // console.log('[#] ');
        // , onmessage
        aiPlay({sign, pos: [x, y]});
      }
		};
  };

  return (
    <div className='game-panel'>
      {loading && <div className="goban-spin"><Spin tip="..." /></div>}
      <div className='message-container'>{message.length > 0 && <Alert message={message} type="error" />}</div>
        <Goban
          board={board}
          signMap={board.signMap}
          animate={true}
          vertexSize={24}
          onVertexMouseUp={vertexEvent}
          showCoordinates={showCoordinates}
          fuzzyStonePlacement={fuzzyStonePlacement}
          isBusy={loading}
        />
      <OperatorPanel mode={mode} futureBoardList={futureBoardList} setFutureBoardList={setFutureBoardList} historyBoardList={historyBoardList} setHistoryBoardList={setHistoryBoardList} gameStatus={gameStatus} board={board} setBoard={setBoard} shareData={shareData} startGame={startGame} setStartGame={setStartGame} lastNode={lastNode} setLastNode={setLastNode}/>
    </div>
  );
};
