import {createAction, createReducer} from '@reduxjs/toolkit';

const open = createAction('kataData/open');
const close = createAction('kataData/close');
const addData = createAction('kataData/addData');
const reset = createAction('kataData/reset');
const deleteOne = createAction('kataData/deleteOne');
const gameStatus = createAction('kataData/gameStatus');
const gobackN = createAction('kataData/gobackN');


const initialState = {kataDataStatus: false, kataData: [], gobackN: null, gameStatus: null};

const dataReducer = createReducer(initialState, (builder) => {
  builder
    .addCase(open, (state, action) => {
      state.kataDataStatus = true;
    })
    .addCase(close, (state, action) => {
      state.kataDataStatus = false;
    })
    .addCase(addData, (state, action) => {
      const {kataData = null} = action;
      // console.log('[*] KataData: push new data', kataData);
      if (kataData) {
        console.log(kataData);
        state.kataData.push(kataData);
      }
    })
    .addCase(reset, (state, action) => {
      state.kataData = [];
    })
    .addCase(deleteOne, (state, action)=>{
      if (state.kataData.length > 0) {
        state.kataData.pop();
      }
    }).addCase(gameStatus, (state, action) => {
      const {gameStatus} = action;
      state.gameStatus = gameStatus;
    }).addCase(gobackN, (state, action) => {
      const {id = null} = action;
      if (!id) {
        console.log('[*] No id found, can not go back');
        return;
      }
      state.gobackN = id;
    });
});

export default dataReducer;
