import React, {useState} from 'react';
import './style.css';

import {useMount} from 'ahooks';
import {useHistory} from 'react-router-dom';

import {EmptyMap, GameMode, webSocketAddr} from '../../../util/constant.js';
import {checkAuth} from '../../../api/user';
import WebSocketHelper from '../../../util/websocketHelper';
import {useTranslation} from 'react-i18next';
import {windowModalComp} from '../../../util/helper';
import store from '../../../store/store';

// 
import CustomGobangBoard from '../../gobanboard/index';
import LeftPanel from '../../leftpanel';
import RightPanel from '../../rightpanel';
import {Modal, Spin} from 'antd';
import {Provider} from 'react-redux';


const Home = (props) => {
  const [gameMode, setGameMode] = useState(GameMode.HumanVSComputer);

  const history = useHistory();
  const [ws, setWs] = useState(null);
  const [childrenState, setChildrenState] = useState({});
  const [childrenStateFn, setChildrenStateFn] = useState({});
  useMount(async () => {
    console.log('[LifeCycle] Home Mount');
    const res = await checkAuth();
    if (!res.success) {
      // 
      windowModalComp('', '', () => {
          history.push('/login');
      });
      // Modal.info({
      //   title: '',
      //   content: '.',
      //   onOk: () => {
      //     history.push('/login');
      //   },
      // });
    }
    const event = {
      open: function(event) {
        console.log('[WebSocket] WebSocket connection open');
      },
      close: function(event) {
        console.log('[WebSocket] Connection closed.');
      },
      error: function(event) {
        console.log('[WebSocket] Error:', event);
      },
    };
    const wsHelper = new WebSocketHelper(webSocketAddr, event, true);
    setWs(wsHelper.ws);
    // ws = {webSocketAddr}; //test
  });

  // useEffect(() => {
  //   if (ws && childrenStateFn.setBoard && childrenStateFn.getBoard) {
  //     // ,message
  //     console.log('[WebSocket] message');
  //     ws.addEventListener('message', Battle.defineMessage({childrenState, childrenStateFn}));
  //   }
  // }, [ws, childrenState, childrenStateFn]);

  const uploadState = (state, setStateFn) => {
    console.log('[]', state, setStateFn);
    state && setChildrenState({...childrenState, ...state});
    setStateFn && setChildrenStateFn({...childrenStateFn, ...setStateFn});
  };

  return (
    <Provider store={store} >
    <div className='home'>
      {
      !ws &&
        <div className='home-spin'>
          <Spin tip="..." />
        </div>
      }
      <LeftPanel
        changeGameMode={setGameMode}
        mode={gameMode}
        ws={ws}
      />
      <CustomGobangBoard
        setLoading={props.setLoading}
        signMap={EmptyMap}
        showCoordinates={true}
        fuzzyStonePlacement={false}
        mode={gameMode}
        ws={ws}
        uploadState={uploadState}
      />
      <RightPanel />
    </div></Provider>);
};
export default Home;
