import {Modal} from 'antd';
import i18n from '../i18n';
import enUsTrans from '../assets/locales/en-us.json';
import zhCnTrans from '../assets/locales/zh-cn.json';
import zhTcTrans from '../assets/locales/zh-tc.json';
import jaJpTrans from '../assets/locales/ja-jp.json';
import koKrTrans from '../assets/locales/ko-kr.json';

const isObject = (obj) => {
  return Object.prototype.toString.call(obj) === '[object Object]';
};

const str2ab = (str) => {
  const buf = new ArrayBuffer(str.length*2); // 2
  const bufView = new Uint16Array(buf);
  for (let i=0, strLen=str.length; i<strLen; i++) {
    bufView[i] = str.charCodeAt(i);
  }
  return buf;
};

const get10TimeStamp = () => {
  return String(new Date().getTime()).substring(0, 10);
};

// 
// const windowModalComp = (title = '', content, finishFn, cancelFn, okText, cancelText) => {
const windowModalComp = (title, content, finishFn, cancelFn, okText, cancelText) => {
  const tTitle = translate(title);
  const tContent = translate(content);
  const tFinishFn = translate(finishFn);
  const tCancelFn = translate(cancelFn);
  const tOkText = translate(okText);
  const tCancelText = translate(cancelText);
  if (cancelFn && cancelText) {
    Modal.confirm({
      // [DEMO] : translate() --> ; 
      title: tTitle,
      content: tContent,
      onOk: tFinishFn,
      onCancel: tCancelFn,
      okText: tOkText,
      cancelText: tCancelText,
    });
    return;
  }
  // 
  Modal.info({
    title: tTitle,
    content: tContent,
    onOk: tFinishFn,
    onCancel: tCancelFn,
    okText: tOkText,
    cancelText: tCancelText,
  });
};

// push
const stackpush = (arr, el, len = 10) => {
  if (!arr) {
    return null;
  }
  if (arr.length === len) {
    // ,
    arr.shift();
    arr.push(el);
  } else {
    arr.push(el);
  }
};

const translate = (keyWord) => {
  const lang = getCurrentLanguage() || zhCnTrans; // 
  let trans = zhCnTrans;
  if (lang === 'zhCn' || lang === 'zh' || lang === 'zh-CN') {
    trans = zhCnTrans;
  } else if (lang === 'en' || lang === 'en-US') {
    trans = enUsTrans;
  } else if (lang === 'zhTc' || lang === 'zh-TC') {
    trans = zhTcTrans;
  } else if (lang === 'ja') {
    trans = jaJpTrans;
  } else if (lang === 'ko') {
    trans = koKrTrans;
  }
  return trans[keyWord]?trans[keyWord]:keyWord;
};

const getCurrentLanguage = () => {
  return i18n.language;
};

export {
  isObject,
  str2ab,
  get10TimeStamp,
  windowModalComp,
  stackpush,
  translate,
};
